<template>
    <div class="Logpass">
         <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">重置密码</p>
            <div slot="last"></div>
        </Header>

        <div class="Logpass_div">
            <p>
                <input type="text" placeholder="账号" v-model="accnum">
            </p>
             <p>
                <input type="text" placeholder="旧密码" v-model="oldpass">
            </p>
             <p>
                <input type="text" placeholder="请输入新密码" v-model="newpass">
            </p>
             <p>
                <input type="text" placeholder="请确认密码" v-model="conpass">
            </p>
            <!-- 验证码 -->
            <p>
                <input type="text" v-model="verificationCode" placeholder="验证码">
                <img :src='code'>
            <!-- 验证码换码 -->
                <ul @click="Huan()">
                    <li>看不清</li>
                    <li>换一张</li>
                </ul>
            </p>
            
        </div>
        <!-- 确认修改 -->
        <div class="Logpass_button">
             <p>
                <input type="button" value="确认修改" @click="UpdaAcknow()">
            </p>
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
import {Dialog} from 'vant'
export default {
    components:{
        Header,
    },
   data() {
      return {
        //   验证码
          verificationCode:"",
          code:"",
        //   账号
          accnum:"",
        //   旧密码
          oldpass:"",
        //   新密码
          newpass:"",
        //   确认密码
          conpass:"",
      }
   },
   created() {
       this.Huan()
   },
    methods: {
        //验证码
        Huan(){
           this.axios.post('http://elm.cangdu.org/v1/captchas',{}).then((res)=>{
                if(res.data.code!=""){
                    this.code=res.data.code
                }
            })
        },
        //确认修改按钮
        UpdaAcknow(){
            if(this.accnum!="" && this.oldpass!="" && this.newpass!="" && this.conpass!=""){
                if(this.newpass==this.conpass){
                    this.axios.post("http://elm.cangdu.org/v2/changepassword",{
                        captcha_code:this.verificationCode,
                        confirmpassword:this.conpass,
                        newpassword:this.newpass,
                        oldpassWord:this.oldpass,
                        username:this.accnum
                        }).then((res)=>{
                            // console.log(res.data)
                        if(res.data.message=='未找到当前用户'){
                            Dialog.confirm({
                            message: res.data.message
                            })
                        }
                        if(res.data.message=='"验证码不正确"'){
                            Dialog.confirm({
                            message: res.data.message
                            })
                        }else{
                            Dialog.alert({
                            message: '密码修改成功'
                            })
                        }
                        })
                }else{
                    Dialog.confirm({
                    message: '两次密码不一致'
                    })
                }
            }else{
                Dialog.confirm({
                message: '有数据为空'
                })
            }
        }
    }
}
</script>

<style lang="scss">
body{
    background: #F5F5F5;
}
.Logpass{
    width: 100%;
    .Logpass_div{
        width: 100%;
        margin-top: 1rem;
        p{
            font-size: 0.3rem;
            background: white;
            border-bottom: 2px solid #F5F5F5;
            padding-left: 0.2rem;
            input{
                line-height: 0.85rem;
                border: none
            }
        }
        >p:nth-of-type(5){
            display: flex;
            font-size: 0.3rem;
                img{
                    margin-left: 0.6rem;
                }
                input{
                    width:55%;
                }
                ul{
                    li:nth-of-type(2){
                        color: #3399ff;
                    }
                }
        }
    }
    .Logpass_button{
        width: 100%;
        p{
            width: 100%;
            text-align: center;
            input{
                width: 95%;
                background: #4CD964;
                font-size: 0.35rem;
                color: white;
                border: none;
                border-radius: 0.05rem;
                height: 0.7rem;
            }
        }
    }
}
</style>


